.diagram {
	margin-top: 20px;
	padding: 1rem;
	border-radius: 12px;
	background-color: var(--gray-200);


	&Wrapper {
		display: flex;
		justify-content: space-around;
		height: 10rem;
	}

	&Btn {
		border: 1px solid var(--gray-50);
		background-color: var(--gray-500);
		color: var(--gray-50);
		border-radius: 4px;
		margin-top: 10px;
		padding: 3px 10px;
		cursor: pointer;
		font-weight: 700;
	}



}